import React, {useState, useEffect} from 'react';
import {useNavigate} from 'react-router-dom';
import axios from 'axios'
import { useCookies } from 'react-cookie';

const LoginComponent = (props) => {
    
    const [cookies, setCookie] = useCookies([]);
    const [memberId, setMemberId] = useState("");
    const [memberPw, setMemberPw] = useState("");
    const navigate = useNavigate();
    
    const CheckLogin = (e) => {
        const checkloginUrl = '/api/opentalk/member/login'

        if (memberId === ""){
            alert(" .")
        }
        else if (memberPw === ""){
            alert(" .")
        }
        else{
            axios.post(checkloginUrl, {
                "id":"null",
                "memberId": memberId,
                "memberPassword": memberPw,
                "memberName": "null",
                "memberNickName": "null",
                "memberEmail": "null",
                "joinDate": "null"
            })
            .then((res) => {
                if (res.status === 200){
                    setCookie("accessToken", res.data.accessToken);
                    navigate("/opentalk/main");
                }
            })
            .catch((error)=>{
                if (error.response && error.response.status === 401) {
                    alert("   .");
                } else {
                    console.error('Error:', error);
                }
            });
        }
        
    }

    const InputId = (e) => {
        setMemberId(e.target.value);
    }

    const InputPw = (e) => {
        setMemberPw(e.target.value);
    }

   return (
    <div>
        <h2></h2>
        <div>
            <label for="memberId">:</label>
            <input type='text' name='memberId' className="memberId" onChange={InputId}></input>
            <br></br>
            <label for="memberPassword">:</label>
            <input type="password" name="memberPassword" className='memberPassword' onChange={InputPw}></input>
            <br></br>
            <label>
                <input type='submit' value="" onClick={CheckLogin}></input>
            </label>
            <label>
                <input type='submit' value=" " onClick={()=>navigate("/opentalk/member/findId")}></input>
            </label>
            <label>
                <input type='submit' value=" " onClick={()=>navigate("/opentalk/member/authId")}></input>
            </label>
            <label>
                <input type='submit' value="" onClick={()=>navigate("/opentalk/member/enroll")}></input>
            </label>
            <br></br>
            <button onClick={()=>navigate("/opentalk/front")}></button>
        </div>
    </div>

    );
}

export default LoginComponent;