import React, {useState} from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const FindMemberComponent = () => {
    const [memberEmail, setMemberEmail] = useState('');
    const [authNum, setAuthNum] = useState('');
    const navigate = useNavigate();

    const GetInputEmail = (event) =>{
        setMemberEmail(event.target.value);
    }

    const CheckMail = () =>{
        const checkUrl = `/api/opentalk/findId/mailSend`
        axios.post(checkUrl, {
            email: memberEmail,
            sendType: "findId"
        }).then((res)=>{
            setAuthNum(res.data);
        })
    }

    const CheckAuth = () =>{
        const checkUrl = `/api/opentalk/findId/mailauthCheck`
        axios.post(checkUrl, {
            email: memberEmail,
            authNum: String(authNum)
        }).then((res)=>{
            if (res.data === "ok"){
                axios.post(`/api/opentalk/member/findId/${memberEmail}`, {
                    memberEmail: memberEmail
                })
                .then((res) => {
                    alert(`  ${res.data} .`)
                    navigate("/opentalk/member/login")
                })
                .catch((error)=>console.log(error))
                
            }
            else{
                alert(" .  .")
            }
        }).catch((error) => console.log(error))
    }
    return(
        <div>
            <h2> </h2>
            <h3> </h3>
            <label>
                : <input type="email" value={memberEmail} onChange = {GetInputEmail}></input><input type="button" value=" " onClick={CheckMail}></input>
                <br></br>
                : <input type="text"></input><input type="button" value="" onClick={CheckAuth}></input>
            </label>
            
        </div>
    );
}

export default FindMemberComponent;