import React, {useState} from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
const EnrollComponent = (props) =>{
    const [memberId, setMemberId] = useState('');
    const [memberPw, setMemberPw] = useState('');
    const [memberName, setMemberName] = useState('');
    const [memberNickName, setMemberNickName] = useState('');
    const [memberEmail, setMemberEmail] = useState('');
    const [authNum, setAuthNum] = useState('');

    const [checkId, setCheckId] = useState(false);
    const [checkNickName, setCheckNickName] = useState(false);
    const [checkEmail, setCheckEmail] = useState(false);
    const [errorMessage, setErrorMessage] = useState('');

    const navigate = useNavigate();

    const GetInputId = (event) =>{
        setMemberId(event.target.value);
    }

    const GetInputPw = (event) =>{
        setMemberPw(event.target.value);
    }

    const GetInputName = (event) =>{
        setMemberName(event.target.value);
    }

    const GetInputNickName = (event) =>{
        setMemberNickName(event.target.value);
    }

    const GetInputEmail = (event) =>{
        setMemberEmail(event.target.value);
    }

    const CheckIdDuplicate = () => {
        const data = new FormData();
        data.append("memberId", memberId);
        const checkUrl = `/api/opentalk/signup/checkId`;
        axios.post(checkUrl, data).then((res)=>{
            if (res.data === true){
                alert(" .");
                setCheckId(false);
            }
            else{
                alert("  .");
                setCheckId(true);
            }
        }).catch((error)=>console.log(error))
    }

    const CheckNickNameDuplicate = () =>{
        const data = new FormData();
        data.append("memberNickName", memberNickName);
        const checkUrl = `/api/opentalk/signup/checkNickName`;
        axios.post(checkUrl, data).then((res)=>{
            if (res.data === true){
                alert(" .");
                setCheckNickName(false);
            }
            else{
                alert("  .");
                setCheckNickName(true);
            }
        }).catch((error)=>console.log(error))
    }

    const CheckMail = () =>{
        const duplicateUrl = '/api/opentalk/signup/checkEmail'
        const data = new FormData();
        data.append("memberEmail", memberEmail);
        axios.post(duplicateUrl, data)
        .then((res)=>{
            if (res.data === true){
                alert("  .");
            }
            else{
                const checkUrl = `/api/opentalk/enroll/mailSend`
                axios.post(checkUrl, {
                    email: memberEmail,
                    sendType: "enroll"
                }).then((res)=>{
                    setAuthNum(res.data);
                })
            }
        })
        .catch((error) => console.log(error));
    }

    const CheckAuth = () =>{
        const checkUrl = `/api/opentalk/enroll/mailauthCheck`
        axios.post(checkUrl, {
            email: memberEmail,
            authNum: String(authNum)
        }).then((res)=>{
            if (res.data === "ok"){
                alert(".");
                setCheckEmail(true);
            }
            else{
                if (checkEmail){
                    alert(" .");
                }
                else{
                    alert(" .  .");
                    setCheckEmail(false);
                }
            }
        })
    }

    const CheckAll = () =>{
        if (!checkId){
            alert("    .");
        }
        else if (!checkNickName){
            alert("    .");
        }
        else if (!checkEmail){
            alert("   .");
        }
        else{
            const url = `/api/opentalk/member/signup`;
            axios.post(url,{
                memberId: memberId,
                memberPassword: memberPw,
                memberName: memberName,
                memberNickName: memberNickName,
                memberEmail: memberEmail
            }).then((res)=>{
    
            }).catch((error)=> {
                if (error.response){
                    setErrorMessage(error.response.data);
                } else{
                    setErrorMessage("");
                    console.log(error);
                }
            });
            alert(" .")
            navigate("/opentalk/member/login");
        }
        
    }

    return(
        <div>
            <h2></h2>
            : <label>
            <input
                type="text"
                value={memberId}
                onChange={GetInputId}></input>
            <input type="button" value="  " onClick={CheckIdDuplicate}></input>
            </label>
            <br></br>
            : <label>
                <input
                type="password"
                value={memberPw}
                onChange={GetInputPw}></input>
            </label>
            <br></br>
            : <label>
                <input
                type="text"
                value={memberName}
                onChange={GetInputName}></input>
            </label>
            <br></br>
            : <input
            type="text"
            value={memberNickName}
            onChange={GetInputNickName}></input>
            <input type="button" value="  " onClick={CheckNickNameDuplicate}></input>
            <br></br>
            : <input
            type="email"
            value={memberEmail}
            onChange={GetInputEmail}></input>
            <input type="button" value="  " onClick={CheckMail}></input>
            <br></br>
            : <input type="text"></input><input type="button" value="" onClick={CheckAuth}></input>
            <br></br>
            <input type="button" value="" onClick={CheckAll}></input>
        </div>
    );
}
export default EnrollComponent;