import React, {useState, useEffect} from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useCookies } from 'react-cookie';
import SetRoomComponent from './setroom';
import RoomComponent from './room';
import {createBrowserHistory} from "history";

const MainComponent = () => {
    const ChatRoomRole = {
        PARTICIPATE: 'PARTICIPATE',
        MANAGER: 'MANAGER'
    };

    const [cookies, setCookie, removeCookie] = useCookies(['accessToken']);
    const [member, setMember] = useState("");
    const [chatList, setChatList] = useState([]);
    const [role, setRole] = useState();
    const [selectManu, setSelectManu] = useState("");
    const [searchKeyword, setSearchKeyword] = useState("");
    const [refresh, setRefresh] = useState(1);
    const naviagte = useNavigate();

    const history = createBrowserHistory();

    // useEffect(() => {
    //     let unlisten = history.listen((location) => {
    //         if (history.action === "POP")
    //     })

    //     return () => {
    //         unlisten();
    //     };
    // }, [history])

    const GetInputSearchKeyword = (event) => {
        setSearchKeyword(event.target.value);
    }

    const selectMenuHandle = (event) => {
        setSelectManu(event.target.value);
    }

    const search = () => {

    }

    const deleteRoom = ({roomInfo}) => {
        if (window.confirm(" ?")){
            const deleteUrl = "/api/opentalk/deleteRoom";
            const data = new FormData();
            data.append("room_id", roomInfo.roomId);
            if (!roomInfo.existLock){
                axios.post(deleteUrl, data)
                .then((res) => {
                    if (res.status === 200){
                        window.alert(" .");
                    }
                })
                .catch((error) => console.log(error));
            }   
            else{
                const inputPassword = window.prompt(" .");
                if (inputPassword === ""){
                    window.alert(" .")
                }
                else{
                    axios.post(deleteUrl + `/${inputPassword}`, data)
                    .then((res)=> {
                        if (res.data === true){
                            window.alert(" .");
                        }
                        else{
                            window.alert(" .");
                        }
                    })
                    .catch((error) => console.log(error));
                }
            }
        }
    }

    const EnterRoom = ({roomInfo, talker}) => {
        const enterUrl = '/api/opentalk/enterRoom';
        if (!roomInfo.existLock){
            let currentRole;
            console.log(roomInfo);
            if (roomInfo.manager === talker.memberNickName){
                currentRole = ChatRoomRole.MANAGER;
            }
            else{
                currentRole = ChatRoomRole.PARTICIPATE;
            }
            setRole(currentRole);
            axios.post(enterUrl, {
                chatroom: roomInfo, 
                member: talker,
                role:role
            })
            .then((res) => {
                if (res.status === 200){
                    naviagte(`/opentalk/room/${roomInfo.roomId}`);
                }
            })
            .catch((error) => console.log(error));
        }
        else{
            const inputPassword = window.prompt(" .");
            console.log(inputPassword);
            if (inputPassword === ""){
                window.alert(" .")
            }else{
                axios.post(enterUrl + `/${inputPassword}`, {
                    chatroom: roomInfo, 
                    member: talker,
                    role:role
                })
                .then((res) => {
                    if (res.data === true){
                        naviagte(`/opentalk/room/${roomInfo.roomId}`);
                    }
                    else{
                        window.alert(" .")
                    }
                })
                .catch((error) => console.log(error));
            }
           
        }
        return (
            <div>
                <RoomComponent roomInfo={roomInfo} talker={talker}/>
            </div>
        );
    }

    useEffect(() => {
        const fetchMainData = async () => {
            try{
                const meResponse = await axios.get('/api/opentalk/member/me', {
                    headers: {Authorization: 'Bearer ' + cookies.accessToken}
                });
                setMember(meResponse.data);
                const roomResponse = await axios.get('/api/opentalk/rooms');
                setChatList(roomResponse.data);
                console.log(roomResponse.data);
            } catch (error){
                console.error(error);
            }
        };

        fetchMainData();
    }, [refresh]);

    const GoProfile = () => {
        if (cookies.accessToken){
            naviagte("/opentalk/profile");
        }
    }

    const LogOut = () => {
        if (cookies.accessToken){
            if (window.confirm(" ?")){
                removeCookie('accessToken');
                window.alert(" .");
                naviagte("/opentalk/front");
            }
        }
        else{
            alert(" .");
            naviagte("/opentalk/front");
        }
        
    };

   return (
    <div>
        <table>
            <img alt=" " src={`${process.env.PUBLIC_URL}/profile_prototype.jpg`}></img>
            <p>, {member.memberNickName}</p>
            <ul>
                {chatList.map(room=>(
                    <li key={room.roomId}>{room.roomName} | : {room.participates} / {room.limitParticipates}
                    {room.existLock && <img alt=" " src={`${process.env.PUBLIC_URL}/lock.jpg`} width={20}></img>}
                    <br></br>{room.introduction}
                    <br></br>: {room.roomManager}
                    <ul>
                        {room.roomTags.map(tag=>(
                            <li>#{tag.tagName}</li>
                        ))}
                    </ul>
                    <button onClick={() => EnterRoom({roomInfo: room, talker: member})}></button>
                    {room.roomManager === member.memberNickName && (
                    <button onClick={() => deleteRoom({roomInfo: room})}></button>
                )}</li>
                ))}
            </ul>
        </table>
        <SetRoomComponent />
        <button onClick={GoProfile}> </button>
        <button onClick={LogOut}></button>
        <br></br>
        <select value={selectManu} onChange={selectMenuHandle}>
            <option value="title"></option>
            <option value="tag"></option>
            <option value="manager"></option>
        </select>
        <input 
            type="text"
            value={searchKeyword}
            onChange={GetInputSearchKeyword}
        ></input>
        <button onClick={search}></button>
    </div>

    );
}

export default MainComponent;