import React, {useState, useEffect} from 'react';
import {useCookies} from 'react-cookie';
import Modal from 'react-modal';
import axios from'axios';

export const SetRoomComponent = () =>{
    const [isOpen, setIsOpen] = useState(false);
    const [roomName, setRoomName] = useState("");
    const [roomId, setRoomId] = useState("");
    const [members, setMembers] = useState();
    const [participants, setParticipants] = useState(3);
    const [existLock, setExistLock] = useState(false);
    const [password, setPassword] = useState("");
    const [manager, setManger] = useState();
    const [info, setInfo] = useState("");
    const [tag, setTag] = useState("");
    const [tags, setTags] = useState([]);

    const [cookies] = useCookies(["accessToken"]);

    useEffect(() => {
        const fetchManager = async () =>{
            try{
                const response = await axios.get("/api/opentalk/member/me", {
                    headers: {Authorization: 'Bearer ' + cookies.accessToken}
                })
                setManger(response.data);
            } catch (error) {
                console.log(error);
            }
        }
        fetchManager();
    }, []);
    
    const openModal = () => {
        setIsOpen(true)
    };
    const closeModal = () => {
        setRoomName('');
        setExistLock(false);
        setPassword('');
        setParticipants(0);
        setIsOpen(false);
        setTags([]);
        setInfo('');
    }

    const GetInputName = (event) => {
        setRoomName(event.target.value);
    }
    const GetInputParticipates = (event) => {
        if (event.target.value >= 3){
            if (event.target.value > 20){
                window.alert("   20 .");
            }
            else{
                setParticipants(event.target.value);
            }
        }
        else{
            window.alert("   3 .");
        }
    }
    const GetInputPassword = (event) => {
        setPassword(event.target.value);
    }
    const GetCheckExistPw = (event) => {
        setExistLock(event.target.checked);
        if (!event.target.checked){
            setPassword('');
        }
    }
    const GetInputInfo = (event) => {
        setInfo(event.target.value);
    }
    const GetInputTag = (event) => {
        setTag(event.target.value);
    }

    const AppendTag = (getTag) => {
        let isExist = false;
        for (let i = 0; i < tags.length; i++){
            if (tags[i].tagName === getTag){
                isExist = true;
            }
        }
        if (tags.length >= 5){
            window.alert("   5  .");
        }
        else{
            if (getTag === ""){
                window.alert(" .");
            }
            else if (isExist){
                window.alert("  .");
            }
            else{
                const newTags = [...tags, {
                    tagName: getTag,
                    accumulate: 0
                }];
                setTags(newTags);
                setTag("");
            }
        }
    }

    const tagDelete = (getTag) => {
        const newTags = tags.filter(tag => tag !== getTag);
        setTags(newTags);
    }


    const MakeRoom = () => {
        const makeUrl = `/api/opentalk/makeRoom`
        axios.post(makeUrl, {
            "roomName": roomName,
            "roomPassword": password,
            "roomManager": manager.memberNickName,
            "limitParticipates": participants,
            "introduction": info,
            "existLock": existLock,
            "members": [],
            "roomTags": tags
        })
        .then((res)=>{
            if (res.status === 200){
                alert(" .");
                setRoomId(res.data);
                closeModal();
            }
        })
        .catch((error) => console.log(error));
    };

    return (
        <div>
            <button onClick={openModal}> </button>
            <Modal isOpen={isOpen} onRequestClose={closeModal}>
                <div>
                     : <input 
                        type="text" 
                        value={roomName} 
                        onChange={GetInputName}></input>
                    <br></br>
                    : <input 
                        type="number" 
                        min="3"
                        max="20"
                        value={participants} 
                        onChange={GetInputParticipates}></input>
                    <br></br>
                    : <input 
                        type="checkbox" 
                        checked={existLock}
                        onChange={GetCheckExistPw}></input>
                    <input 
                        type="password" 
                        value={password} 
                        onChange={GetInputPassword}
                        disabled={!existLock}>   
                    </input>
                    <br></br>
                    <input 
                        type="text" 
                        value={info} 
                        placeholder={" "}
                        size={100}
                        onChange={GetInputInfo}>
                    </input>
                    <br></br>
                    <input 
                        type="text" 
                        value={tag} 
                        onChange={GetInputTag}>    
                    </input>
                    <input
                        type="button"
                        value=" "
                        onClick={()=>AppendTag(tag)}>
                    </input>
                    {tags.map((t)=> (
                        <li>#{t.tagName}<button onClick={()=>tagDelete(t)}></button></li>
                    ))}
                    <br></br>
                    <input type="submit" value=" " onClick={MakeRoom}></input>
                    <button onClick={closeModal}> </button>
                </div>
                
            </Modal>
        </div>
    );
}; 

export default SetRoomComponent;