import React, {useState, useEffect} from 'react';
import {useCookies} from 'react-cookie';
import Modal from 'react-modal';
import axios from'axios';

const ChangRoomComponent = ({room_Id}) => {
    const [roomInfo, setRoomInfo] = useState();

    const [isOpen, setIsOpen] = useState(false);
    const [roomName, setRoomName] = useState("");
    const [preRoomName, setPreRoomName] = useState("");

    const [info, setInfo] = useState("");
    const [preInfo, setPreInfo] = useState("");

    const [existLock, setExistLock] = useState(false);
    const [preExistLock, setPreExistLock] = useState(false);

    const [password, setPassword] = useState("");
    const [prePassword, setPrePassword] = useState("");

    const [tag, setTag] = useState("");
    const [tags, setTags] = useState([]);
    const [preTags, setPreTags] = useState([]);

    const [participants, setParticipants] = useState(0);
    const [preParticipates, setPreParticipants] = useState(0);

    useEffect(() =>{
        const fetchCurRoomInfo = async () => {
            try{
                const response = await axios.get(`/api/opentalk/getRoom/${room_Id}`);
                setRoomInfo(response.data.chatroom);
                setPreRoomName(response.data.chatroom.roomName);
                setPreExistLock(response.data.chatroom.existLock);
                setPreInfo(response.data.chatroom.introduction);
                setPrePassword(response.data.chatroom.roomPassword);
                setPreParticipants(response.data.chatroom.limitParticipates);
                setPreTags(response.data.chatroom.roomTags);
            } catch (error) {
                console.log(error);
            }
        }
        fetchCurRoomInfo();
    }, []);

    useEffect(() => {
        setRoomName(preRoomName);
        setExistLock(preExistLock);
        setInfo(preInfo);
        setPassword(prePassword);
        setParticipants(preParticipates);
        setTags(preTags);
    }, []);

    const openModal = () => {
        setIsOpen(true);
    }

    const closeModal = () => {
        setIsOpen(false);
    }

    const cancleSetModal = () => {
        setIsOpen(false);
        setRoomName(preRoomName);
        setExistLock(preExistLock);
        setInfo(preInfo);
        setPassword(prePassword);
        setParticipants(preParticipates);
        setTags(preTags);
    }

    const changeRoomModal = () => {
        axios.post("/api/opentalk/changeRoom", {
            roomId: roomInfo?.roomId,
            roomName: roomName,
            roomPassword: password,
            limitParticipates: participants,
            introduction: info,
            existLock: existLock,
            roomTags: tags            
        })
        .then((res) => {
            if (res.data === true){
                window.alert("  .");
            }
        })
        .catch((error) => console.log(error));
        setIsOpen(false);
    }

    const GetInputName = (event) => {
        setRoomName(event.target.value);
    }
    const GetInputParticipates = (event) => {
        if (event.target.value >= preParticipates){
            if (event.target.value > 20){
                window.alert("   20 .");
            }
            else{
                setParticipants(event.target.value);
            }
        }
        else if (event.target.value >= 3 && event.targat.value < preParticipates){
            window.alert("     .");
        }
        else {
            window.alert("   3 .");
        }
    }
    const GetInputPassword = (event) => {
        setPassword(event.target.value);
    }
    const GetCheckExistPw = (event) => {
        setExistLock(event.target.checked);
        if (!event.target.checked){
            setPassword('');
        }
    }
    const GetInputInfo = (event) => {
        setInfo(event.target.value);
    }
    const GetInputTag = (event) => {
        setTag(event.target.value);
    }

    const AppendTag = (getTag) => {
        let isExist = false;
        for (let i = 0; i < tags.length; i++){
            if (tags[i].tagName === getTag){
                isExist = true;
            }
        }
        if (tags.length >= 5){
            window.alert("   5  .");
        }
        else{
            if (getTag === ""){
                window.alert(" .");
            }
            else if (isExist){
                window.alert("  .");
            }
            else{
                const newTags = [...tags, {
                    tagName: getTag,
                    accumulate: 0
                }];
                setTags(newTags);
                setTag("");
            }
        }
    }

    return(
        <div>
            <button onClick={openModal}> </button>
            <Modal isOpen={isOpen} onRequestClose ={closeModal}>
                <div>
                 : <input 
                    type="text" 
                    value={roomName} 
                    onChange={GetInputName}></input>
                    <br></br>
                    : <input 
                        type="number" 
                        min="3"
                        max="20"
                        value={participants} 
                        onChange={GetInputParticipates}></input>
                    <br></br>
                    : <input 
                        type="checkbox" 
                        checked={existLock}
                        onChange={GetCheckExistPw}></input>
                    <input 
                        type="password" 
                        value={password} 
                        onChange={GetInputPassword}
                        disabled={!existLock}>   
                    </input>
                    <br></br>
                    <input 
                        type="text" 
                        value={info} 
                        placeholder={" "}
                        size={100}
                        onChange={GetInputInfo}>
                    </input>
                    <br></br>
                    <input 
                        type="text" 
                        value={tag} 
                        onChange={GetInputTag}>    
                    </input>
                    <input
                        type="button"
                        value=" "
                        onClick={()=>AppendTag(tag)}>
                    </input>
                    {tags?.map((t)=> (
                        <li>#{t.tagName}</li>
                    )
                    )}
                    <br></br>
                <button onClick={changeRoomModal}></button>
                <button onClick={cancleSetModal}> </button>
                </div>
            </Modal>
        </div>
    );
}

export default ChangRoomComponent;